import React, { useEffect } from "react";
import Header from "./components/Header";
import Footer from "./components/Footer";
import Sources from "./data/Sources";
import { Route } from "react-router-dom";
import ReactGA from "react-ga";
import StatePage from "./containers/StatePage";
//import generateSitemap from "./generateSiteMap";
import "./styles.css";
import "./TableStyle.css";

function App() {
  useEffect(() => {
    ReactGA.initialize("UA-166351698-2"); //UA-166351698-2
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);
  //generateSitemap();
  const [darkMode, setDarkMode] = React.useState(false);
  const darkBtn = (
    <i
      className="fas fa-sun"
      style={{
        fontSize: "24px",
        marginRight: "20px",
        marginLeft: "0px",
        color: "#c5c5d3"
      }}
      onClick={() => setDarkMode((prevMode) => !prevMode)}
    />
  );
  return (
    <div className="App">
      <Header btn={darkBtn} txt={darkMode ? "dark" : "light"} />
      <Route path="/data-sources-api" exact component={Sources} />
      <Route path="/about" component={Sources} />
      <Route path="/country/IN/state-UT/:sName" component={StatePage} />
      <Footer />
    </div>
  );
}

export default App;
