import React from "react";
import { Route, NavLink, Switch } from "react-router-dom";
import Home from "../containers/Home";
//import CountryData from "./CountryData";
import India from "../containers/India";
import Patients from "../containers/Patients";
import CovidPage from "../containers/CovidPage";

export default function Header(props) {
  console.log("header props ", props.txt);
  //alert(props.txt);
  return (
    <React.Fragment>
      <nav className="navbar navbar-expand-md navbar-fixed-top ml-auto">
        <div className="navbar-brand">
          <i
            className="fas fa-shield-virus"
            style={{ fontSize: "25px", color: "rgb(14, 129, 14)" }}
          />{" "}
          <a href="https://emrpx.codesandbox.io">
            <span className="text-dark">Covid-19</span>{" "}
            <span className="text-danger">Tracker</span>
          </a>
        </div>
        <button
          className="navbar-toggler bg-light"
          type="button"
          data-toggle="collapse"
          data-target="#collapsibleNavbar"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon">
            <i className="fas fa-bars hamburger" />
          </span>
        </button>
        <div className="collapse navbar-collapse" id="collapsibleNavbar">
          <ul className="navbar-nav">
            <li className="nav-item">
              <NavLink
                to="/"
                className="nav-link"
                data-toggle="collapse"
                data-target="#collapsibleNavbar"
              >
                {" "}
                Home{" "}
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                to="/country/IN"
                className="nav-link"
                data-toggle="collapse"
                data-target="#collapsibleNavbar"
              >
                {" "}
                INDIA{" "}
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                to="/patients"
                className="nav-link"
                data-toggle="collapse"
                data-target="#collapsibleNavbar"
              >
                {" "}
                PATIENTS{" "}
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                to="/covid19"
                className="nav-link"
                data-toggle="collapse"
                data-target="#collapsibleNavbar"
              >
                {" "}
                COVID-19{" "}
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                to="/about"
                className="nav-link"
                data-toggle="collapse"
                data-target="#collapsibleNavbar"
              >
                {" "}
                About
              </NavLink>
            </li>
            <li className="nav-item">{props.btn}</li>
          </ul>
        </div>
      </nav>
      <Switch>
        <Route path="/country/IN" exact component={India} />
        <Route path="/patients" exact component={Patients} />
        <Route
          path="/"
          exact
          render={props => <Home {...props} txt={props.txt} btn={props.btn} />}
        />
        <Route path="/covid19" exact component={CovidPage} />
      </Switch>
    </React.Fragment>
  );
}
